.Toolbar {
    height: 4rem;
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    background: rgb(171, 16, 50);
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 50;
}

