.circle {
    cursor: pointer;
    margin-right: 30px;
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    font-size: 1.5rem;
    color: #ffffff;
    line-height: 3rem;
    text-align: center;
    background: rgb(41,98, 255);
  }
  
